import styled from 'styled-components'
import {MdKeyboardArrowRight,MdArrowForward} from 'react-icons/md'
import {Link} from 'react-scroll'
export const MainContainer = styled.div`
  background: #0c0c0c;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 30px;
  height: 800px;
  position: relative;
  z-index: 1;
  :before{
    content: '' ;
    position: absolute;
    top:0;
    left:0;
    right:0;
    bottom:0;
    background:linear-gradient(180deg,rgba(0,0,0,0.2)0%,rgba(0,0,0,0.6) 100%);
    z-index:1;
  }
`

export const MainBg = styled.div`
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
`

export const VideoBg = styled.video`
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit:cover;
  background:#232a34;
`
export const MainContent = styled.div`
    z-index:3;
  max-width:1200px;
  position:absolute;
  padding: 8px 24px;
  display:flex;
  flex-direction:column;
  align-items:center;
    `

export const MainH1 = styled.h1`
    color:#fff;
  font-size:48px;
  text-align:center;
  
  @media screen and (max-width:768px){
    font-size:40px;
  }
  @media screen and (max-width:480px){
    font-size:32px;
  }
`
export const MainP = styled.p`
    margin-top: 27px;
  color:#fff;
  font-size:24px;
  text-align:center;
  max-width:600px;
  @media screen and (max-width: 768px) {
    font-size: 24px;
  }
  @media screen and (max-width: 480px) {
    font-size: 18px;
  }
`
export const MainBtnWrapper = styled.div`
    margin-top:33px;
  display:flex;
  flex-direction:column;
  align-items:center;
    `

export const ArrowForward = styled(MdArrowForward)`
margin-left:8px;
  font-size:20px;
`
export const ArrowRight = styled(MdKeyboardArrowRight)`
    margin-left:8px;
font-size:20px;

`





export const Button = styled.button`
  border-radius:50px;
  background:#010606;
  white-space:nowrap;
  padding:12px 30px;
  color:#fff;
  font-size:16px;
  outline:none;
  border:none;
  cursor:pointer;
  display:flex;
  justify-content:center;
  align-items:center;
  transition:all 0.2s ease-in-out;
  text-decoration: none;
  margin:1rem;
  &:hover {
    transition:all 0.2s ease-in-out;
    color:black;
    background:#BADA55;
  }
`
